<template>
  <div id="dialog-title">
    <p id="title">{{ name }}</p>
    <p id="price">{{ price }}</p>
    <p id="subtitle" v-if="description">{{ description }}</p>
  </div>
</template>

<script>
export default {
  computed: {
    item() { return this.$store.state.orders.currentItem },
    name() {
      if (this.item === null) return '';
      else return this.item.name;
    },
    price() {
      if (this.item === null) return '';
      else return '$' + parseFloat(this.item.base_price).toFixed(2);
    },
    description() {
      if (this.item === null) return '';
      else return this.item.description;
    }
  },
};
</script>

<style scoped>
p {
  margin: 0px;
}

#dialog-title {
  width: 60%;
}

#title {
  font-weight: bold;
  font-size: 18px;
}

#price {
  font-size: 18px;
}

#subtitle {
  font-size: 14px;
  color: #a0a0a0;
}
</style>
