<template>
  <v-app>
    <toolbar/>

    <v-content
      :style="{ opacity }"
    >
      <router-view/>
    </v-content>
  </v-app>
</template>

<script>
import Toolbar from '@/components/toolbar/MainToolbar.vue';

export default {
  name: 'App',
  components: {
    'toolbar': Toolbar,
  },

  computed: {
    opacity() {
      if (this.$store.state.toolbarOpen) return 0.5;
      else return 1.0;
    },
  },
};
</script>

