<template>
	<div class="mb-3">
		<p id="header">{{ item.name }}</p>
		<v-radio-group hide-details
			class="mt-0 pl-1 text-capitalize"
			v-model="item.selected"
		>
			<v-radio v-for="(option, index) in item.options"
				:key="index"
				:ripple="false"
				:label="formatLabel(option)"
				:value="index"
			/>
		</v-radio-group>
	</div>
</template>

<script>
export default {
	props: ['item'],

	methods: {
		formatLabel(option) {
			let { name, cost } = option;
			cost = cost.toFixed(2);
			return `${name} (+$${cost})`;
		},
	},
};
</script>

<style scoped>
#header {
	margin: 0px;
	font-size: 18px;
}
</style>
