<template>
  <div id="header">
    <span class="title">
      <v-icon medium class="ma-0">location_on</v-icon>
      {{ formatted.title }}
      <v-icon medium class="ma-0">a</v-icon>
    </span>
    <span>{{ formatted.description }}</span>
  </div>
</template>

<script>
export default {
  props: ['title', 'description'],

  computed: {
    formatted() {
      let { title, description } = this;
      return {
        title: title.toUpperCase(),
        description: description.toUpperCase(),
      };
    },
  },
};
</script>

